import './vendor';
import './vendor/datepicker.min';

const $arrow = $('#arrow');
const $yellowSlice = $('.scale__slice--yellow');
const $greenSlice = $('.scale__slice--green');
const $blueSlice = $('.scale__slice--blue');

const scale = (function (count, end) {
	const interval = setInterval(function () {
		if (count < end) {
			count++;
		}

		if (count > end) {
			count--;
		}

		$('.result__number').text(count);

		if (count === end) {
			clearInterval(interval);
		}
	}, 2);
});

$yellowSlice.on('click', () => {
	$arrow.removeClass().addClass('yellow');
	const $count = $('.result__number').text();
	scale($count, 800);
});

$greenSlice.on('click', () => {
	$arrow.removeClass().addClass('green');
	const $count = $('.result__number').text();
	scale($count, 1000);
});

$blueSlice.on('click', () => {
	$arrow.removeClass().addClass('blue');
	const $count = $('.result__number').text();
	scale($count, 1200);
});
